import styled from "styled-components";

export default styled.div`
  font-family: "Open Sans", sans-serif;
  margin: 0 auto;
  font-size: 14px;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: "Montserrat", sans-serif;
  }

  input {
    font-family: "Open Sans", sans-serif;
  }
`;



